@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
  --white: #ffffff;
  --pale-green: #EFFAFA;
  --dark-green: #5CA5A5;
  --black: #000;
  --grey: #7C8F8F;
}

body {
  font-family: "League Spartan", sans-serif;
  background-color: var(--pale-green);
  overflow-x: hidden;
  margin: 0;
}

.App {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bg-img {
  width: 100vw;
  max-height: 150px;
  margin-bottom: 75px;
}

.search-filter {
  background-color: white;
  padding: 25px 40px 25px 40px;
  margin-bottom: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: -120px;
  -webkit-box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.job-listing-container, .job-listing-container-featured {
  width: 70vw;
  background-color: var(--white);
  margin-bottom: 25px;
  padding: 25px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  -webkit-box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.job-listing-container-featured {
  border-left: 5px solid var(--dark-green);
}

.logo-div {
  margin-right: 20px;
}

.job-listing-info {
  display: flex;
  flex-direction: column;
}

.job-listing-info>h1 {
  margin: 0px 0px 0px 0px;
}

.job-listing-info-toprow {
  display: flex;
  align-items: center;
}

.job-listing-info-toprow>* {
  margin-right: 10px;
}

.job-listing-info-bottomrow {
  display: flex;
  color: var(--grey);
}

.job-listing-info-bottomrow>* {
  margin-right: 10px;
}

.tags {
  margin-left: auto;
  display: flex;
  flex-wrap: wrap;
}

.tags-search {
  cursor: pointer;
  margin-right: 15px;
  background-color: var(--pale-green);
  font-weight: bold;
  color: var(--dark-green);
  display: flex;
}

.tags-search>p {
  margin: 10px;
}

.tags>* {
  cursor: pointer;
  margin-right: 15px;
  background-color: var(--pale-green);
  padding: 10px;
  font-weight: bold;
  color: var(--dark-green);
}

.company {
  color: var(--dark-green);
  font-weight: bold;
}

.new {
  background-color: var(--dark-green);
  text-transform: uppercase;
  color: var(--white);
  padding: 7px 13px 5px 13px;
  border-radius: 20px;
  font-weight: bold;
  font-size: .8em;
}

.featured {
  background-color: var(--black);
  text-transform: uppercase;
  color: var(--white);
  padding: 7px 13px 5px 13px;
  border-radius: 20px;
  font-weight: bold;
  font-size: .8em;
}

.remove-tag-div {
  background-color: var(--dark-green);
  display: flex;
  align-items: center;
  padding: 10px;
}

.remove-tag-div:hover {
  background-color: var(--black);
}

.results {
  margin-bottom: 75px;
}

.clear {
  font-weight: bold;
  color: var(--dark-green);
  cursor: pointer;
  margin-left: auto;
}